//@flow

import React from 'react'
import {ContentWithVideos} from '../../ContentWithVideos/ContentWithVideos'
import {Header} from '../../Header/Header'
import {Content} from '../../Content/Content'
import {ClientLogos} from '../../ClientLogos/ClientLogos'
import {TwoColumnRow} from '../../TwoColumnRow/TwoColumnRow'
import {Footer} from '../../Footer/Footer'
import {Reviews} from '../../Reviews/Reviews'

type ReviewT = {
  text: string,
  author: string,
  photo: string,
}

type StudioT = {
  header: {title: string, description: string, src?: string},
  studioA: {
    title: string,
    description: string,
    price: string,
    link: string,
  },
  studioB: {
    title: string,
    description: string,
    price: string,
    link: string,
  },
  liveRecord: {title: string, description: string, price: string, videos: string[]},
  production: {title: string, description: string, price: string, link: string},
  vocalRecord: {title: string, description: string, price: string, link: string},
  toolsRecording: {title: string, description: string, price: string, link: string},
  reviews: Array<ReviewT>,
}

export const Studio = ({
  header,
  studioA,
  studioB,
  liveRecord,
  production,
  vocalRecord,
  toolsRecording,
  reviews,
}: StudioT) => (
  <div>
    <Header {...header} />

    <TwoColumnRow
      left={
        <div>
          <h2>{studioA.title}</h2>
          <p>{studioA.description}</p>
          <p>  {studioA.price}  </p>
          <p>
            <a href={studioA.link}></a>
          </p>
        </div>
      }
      right={
        <div>
          <h2>{studioB.title}</h2>
          <p>{studioB.description}</p>
          <p>  {studioB.price}  </p>
          <p>
            <a href={studioB.link}></a>
          </p>
        </div>
      }
    />

    <ClientLogos />

    <TwoColumnRow
      left={<div></div>}
      right={
        <div>
          <h2>{vocalRecord.title}</h2>
          <p>{vocalRecord.description}</p>
          <p>  {vocalRecord.price}  </p>
          <p>
            <a href={vocalRecord.link}></a>
          </p>
        </div>
      }
    />

    <TwoColumnRow
      invert
      left={
        <div>
          <h2>{toolsRecording.title}</h2>
          <p>{toolsRecording.description}</p>
          <p>  {toolsRecording.price}  </p>
          <p>
            <a href={toolsRecording.link}></a>
          </p>
        </div>
      }
      right={<div></div>}
    />

    {false && (
      <ContentWithVideos videoIds={liveRecord.videos}>
        <h2>{liveRecord.title}</h2>
        <p>{liveRecord.description}</p>
        <p>  {liveRecord.price} .</p>
      </ContentWithVideos>
    )}

    <TwoColumnRow
      left={
        <div>
          <h2>{production.title}</h2>
          <p>{production.description}</p>
          <p>  {production.price}  </p>
          <p>
            <a href={production.link}></a>
          </p>
        </div>
      }
      right={
        <div>
          <p></p>
          <p></p>
          <p></p>
          <p></p>
          <p></p>
        </div>
      }
    />

    <Content>
      <Reviews reviews={reviews} />
    </Content>

    <Content>
      <div>    </div>
    </Content>
    <Footer />
  </div>
)



